@use "../global/variables";

// arabic support
html[lang="ar"] {
  .dropdown__content-end {
    left: 0;
    right: unset;
  }
}
// end arabic support

.dropdown {
  display: inline-block;
  position: relative;

  &__toggle {
    cursor: pointer;
  }

  &__content {
    padding: 4px 0;
    display: none;
    overflow: hidden;
    min-width: 100%;
    top: 100%;
    right: 1rem;
    position: absolute;
    background: white;
    border-radius: 4px;
    box-shadow: 0px 2px 12px 2px rgba(61, 80, 86, 0.2);

    &__arrow {
      display: none;
      bottom: 0;
      right: 2rem;
      position: absolute;
      width: 0.375rem;
      height: 0.375rem;
      border-bottom: 0.375rem solid #fff;
      border-left: 0.375rem solid transparent;
      border-right: 0.375rem solid transparent;
    }

    &.show {
      display: block;
      ~ .dropdown__content__arrow {
        display: block;
      }
    }
  }
}
