.breadcrumb {
  list-style: none;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  a {
    text-decoration: none;
    color: var(--txt-normal);
    display: flex;
    align-items: center;
    justify-content: center;
  }
  span {
    font-size: 18px;
  }
  &__item {
    display: inline-flex;
    justify-content: center;
    align-content: center;
    text-transform: uppercase;
    font-size: 0.75rem;
    font-weight: var(--font-semi-bold);
    letter-spacing: 1px;
    &::before {
      content: "/";
      color: var(--icon-color);
      padding-right: 0.5rem;
    }
    &:first-child::before {
      content: "";
      padding: 0;
    }
    &:last-child {
      color: var(--icon-color);
    }
  }
  &-light {
    a {
      color: #fff !important;
    }
    .breadcrumb {
      &__item {
        color: #fff !important;
      }
      &__item::before {
        color: #fff !important;
      }
    }
  }
}
