@use "../global/variables";

.btn-icon {
  cursor: pointer;
  width: 38px;
  height: 38px;
  letter-spacing: normal;
  text-transform: none;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  color: var(--icon-color);
  border-radius: 4px;
  border: 1px solid rgba($color: #000000, $alpha: 0.1);
  font-weight: var(--font-semi-bold);
  font-size: 0.75rem;
  transition: 0.3s all;
  &:hover {
    text-decoration: none;
    background-color: rgba($color: #000000, $alpha: 0.1);
  }
  &:focus {
    outline: none;
    text-decoration: none;
    box-shadow: 0 0 0 3px var(--border-hover-color);
  }
  span {
    font-size: 18px;
  }
}

.btn-group {
  display: inline-flex;
  justify-content: center;
  gap: 8px;
}

.btn {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  gap: 0.5rem;
  cursor: pointer;
  outline: none;
  border: none;
  background: none;
  padding: 0.5rem 1rem;
  text-transform: uppercase;
  letter-spacing: 1px;
  transition: 0.3s all;
  border-radius: 4px;
  font-weight: var(--font-bold);
  font-size: 0.75rem;
  color: var(--txt-heading-color);
  border: 1px solid rgba($color: #000000, $alpha: 0.1);
  &:focus {
    outline: none;
    text-decoration: none;
    box-shadow: 0 0 0 3px var(--border-hover-color);
  }
  &:hover {
    text-decoration: none;
    background-color: rgba($color: #000000, $alpha: 0.1);
  }
  span {
    font-size: 20px;
  }

  &-circle {
    border-radius: 50%;
  }
  &-rounded {
    border-radius: 5rem;
  }

  &-text {
    border: none;
    &:hover {
      background-color: transparent !important;
    }
  }

  // SIZING
  &-small {
    padding: 0.5em 1em;
    font-size: 0.75rem;
    span {
      font-size: 1rem;
    }
  }

  &-big {
    font-size: 1.5rem;
    span {
      font-size: 28px;
    }
  }
  // end sizing
  &-disabled {
    cursor: not-allowed;
    opacity: 0.3;
  }

  &-full {
    display: flex;
    max-width: 100%;
    width: 100%;
  }
  &-rounded {
    border-radius: 5em;
  }

  // variants
  &-primary {
    color: var(--orange);
  }

  &-secondary {
    color: var(--green);
  }

  &-link {
    color: var(--blue);
  }

  &-light {
    color: #fff;
  }

  // filled button
  &-filled {
    color: #fff;
  }
  &-filled {
    &.btn-primary {
      background-color: var(--orange);
      border-color: var(--orange);
    }
    &.btn-secondary {
      background-color: var(--green);
      border-color: var(--green);
    }
  }

  // border buttons
  &-border {
    &-primary {
      background-color: transparent;
      color: var(--orange);
      border-color: var(--orange);
      &:hover {
        background-color: transparent !important;
      }
    }
    &-secondary {
      background-color: transparent;
      color: var(--green);
      border-color: var(--green);
      background-color: transparent !important;
    }
  }
}
