@use "../global/variables";

// tabs
.tabs-wrapper {
  background-color: #fff;
  padding: 1.5rem;
  border-radius: 3px;
  border: 1px solid var(--border-color);
  .tab {
    display: none;
    &.active {
      display: block;
    }
  }
}

.nav-tabs {
  list-style: none;
  display: flex;
  align-items: center;
  &__item {
  }

  &__link {
    box-sizing: border-box;
    color: var(--txt-heading-color);
    line-height: 1;
    padding: 0.75rem 1.75rem;
    cursor: pointer;
    &:hover,
    &.active {
      font-weight: var(--font-semi-bold);
      color: var(--green);
      box-shadow: 0 3px 0 0 var(--green);
    }
  }
}

.nav-toggler {
  cursor: pointer;
  color: white;
  padding: 1rem;
  border-right: thin solid rgba($color: #000000, $alpha: 0.1);
  &:hover {
    background-color: rgba($color: #000000, $alpha: 0.1);
  }
}

.nav {
  // wrapper
  &-wrapper {
    z-index: 1;
  }
  // separator title
  &__title {
    margin-bottom: 0.75rem;
    color: var(--icon-color);
    font-size: 0.875rem;
    font-weight: var(--font-normal);
    text-transform: uppercase;
    letter-spacing: 1.5px;
  }

  // simple nav
  &-simple {
    display: flex;
    flex-direction: column;
    margin-bottom: 1.25rem;

    // item
    &__item,
    &__subitem {
      font-size: 12px;
      // position: relative;
      & > a {
        text-transform: capitalize;
        font-weight: var(--font-semi-bold);
        color: var(--icon-color);
        letter-spacing: 0.2px;
        padding: 12px;
        line-height: 1;
        display: flex;
        flex-direction: row;
        align-items: center;
        gap: 1rem;
        // transition: 0.3s all;
      }
    }

    &__item {
      border-bottom: thin solid rgba($color: #000000, $alpha: 0.1);
      font-size: 1rem;
      &:hover > a,
      &.active > a {
        transition: all 0.1s;
        border-left: 3px solid var(--green);
        // background-color: rgba($color: #fff, $alpha: 0.1);
        color: var(--green);
        text-decoration: none;
      }

      // &:hover,
      &.active {
        .nav-simple__sublist {
          height: 100%;
        }
      }
      &.has-dropdown {
        position: relative;
        > a::after {
          font-family: "Material Icons";
          font-weight: normal;
          font-style: normal;
          font-size: 24px;
          display: inline-block;
          line-height: 1;
          text-transform: none;
          letter-spacing: normal;
          word-wrap: normal;
          white-space: nowrap;
          direction: ltr;
          -webkit-font-smoothing: antialiased;
          text-rendering: optimizeLegibility;
          -moz-osx-font-smoothing: grayscale;
          font-feature-settings: "liga";

          position: absolute;
          content: "arrow_drop_down";
          right: 12px;
          transform: rotate(-90deg);
        }
        &.active {
          > a::after {
            transform: rotate(0deg);
          }
        }
      }
    }
    // end item

    // sublist
    &__sublist {
      overflow: hidden;
      height: 0px;
      transition: all 0.3s;
      background-color: rgba($color: #000000, $alpha: 0.2);
    }
    // end sublist

    // sub item
    &__subitem {
      font-size: 12px;
      a {
        padding-left: 2.5rem;
        font-weight: var(--font-normal);
      }
      &:not(:last-child) {
        border-bottom: thin solid rgba($color: #000000, $alpha: 0.1);
      }
      &:hover a,
      &.active {
        color: var(--orange);
        text-decoration: none;
      }
    }
    // end subitem

    // icon
    &__icon {
      font-size: 20px;
    }
    // text
    &__text {
      font-size: 0.875rem;
      white-space: nowrap;
      overflow: hidden;
    }
  }
}
