$card-shadow-muted: 0px 1px 1px rgba(61, 80, 86, 0.1),
  0px 1px 8px 0px rgba(61, 80, 86, 0.1);

.table {
  width: 100%;
  border-collapse: collapse;
  tr {
    background-color: white;
    border-bottom: thin solid rgba($color: #000000, $alpha: 0.1);
    &:last-child {
      border-bottom: none;
    }
  }
  td,
  th {
    text-align: left;
    padding: 0.35rem 1rem;
  }

  thead {
    th {
      font-size: 0.825rem;
      color: var(--icon-color);
      font-weight: var(--font-bold);
      // color: var(--txt-heading-color);
      text-transform: capitalize;
      letter-spacing: 0.2px;
      border-bottom: 2px solid rgba($color: #000000, $alpha: 0.1);
    }
  }
  tbody {
    tr {
      &:hover {
        cursor: pointer;
        background: var(--bg);
      }
    }
    td {
      // padding: 8px;
      font-size: 1rem;
    }
  }
}
