@use "../global/variables";

.logo {
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  background-color: var(--green);
  border-right: 1px solid rgba($color: #000000, $alpha: 0.1);
  border-left: 1px solid rgba($color: #000000, $alpha: 0.1);
  font-size: 1.5rem;
  font-weight: var(--font-semi-bold);
  text-shadow: 1px 1px 1px rgba($color: #000000, $alpha: 0.1);
}

.header {
  &__wrapper {
    height: 50px;
    width: 100%;
    display: flex;
    align-items: center;
    position: relative;
    gap: 1rem;
    background-color: var(--green);
  }
  .logo {
    width: 56px;
  }

  &__separator {
    flex: 1;
  }

  &__item {
    display: flex;
    align-items: center;
    color: #fff;
  }

  &__profile {
    margin-right: 1rem;
    padding: 0.5rem 1rem;
    color: white;
    display: flex;
    align-items: center;
    gap: 0.5rem;
    font-weight: var(--font-semi-bold);
    font-size: 0.875rem;
    img {
      width: 32px;
      height: 32px;
      border-radius: 50%;
    }
    &:hover {
      background-color: rgba($color: #000000, $alpha: 0.1);
    }
    &__link {
      display: flex;
      align-items: center;
      padding: 12px 18px;
      text-decoration: none !important;
      gap: 12px;
      font-size: 0.875rem;
      color: var(--txt-heading-color);
      &:hover {
        color: var(--green);
      }
      span {
        font-size: 16px;
      }
    }
  }
}

.profile-image {
  width: 28px;
  height: auto;
  border-radius: 50%;
  box-shadow: 0 0 0 3px #fff;
}
