$card-shadow-muted: 0px 1px 1px rgba(61, 80, 86, 0.1),
  0px 1px 8px 0px rgba(61, 80, 86, 0.1);

.alert {
  display: flex;
  align-items: center;
  gap: 1rem;
  position: relative;
  padding: 0.75rem 1rem;
  margin: 1rem 0;
  border-radius: 3px;
  font-size: 0.875rem;
  background-color: white;
  font-weight: var(--font-semi-bold);
  border-left: 3px solid transparent;
  animation: popup 0.3s ease;
  box-shadow: $card-shadow-muted;
  &__error {
    color: var(--red);
    border-color: var(--red);
  }

  &__success {
    color: var(--green);
    border-color: var(--green);
  }

  @keyframes popup {
    from {
      transform: scale(0.2);
    }
    to {
      transform: scale(1);
    }
  }
}
