$card-shadow: 0px 4px 4px rgba(61, 80, 86, 0.1),
  0px 1px 23px 6px rgba(61, 80, 86, 0.1);

$card-shadow-muted: 0px 1px 1px rgba(61, 80, 86, 0.1),
  0px 1px 1px 0px rgba(61, 80, 86, 0.1);

.card-profile {
  .card__body {
    padding-top: calc(64px + 1.5rem);
    position: relative;
  }
  &__image {
    width: 128px;
    height: 128px;
    border-radius: 50%;
    background: linear-gradient(#fff, #fff);
    border: 4px solid #fff;
    box-shadow: $card-shadow-muted;
    position: absolute;
    top: -64px;
  }
  .card__title {
    font-size: 1.375rem;
    line-height: 1.375rem;
    letter-spacing: normal;
  }
  .card__text {
    font-size: 1rem;
    line-height: 1.5;
  }
}

.card {
  background-color: white;
  color: var(--txt-heading-color);
  box-shadow: $card-shadow;
  border: 1px solid rgba(61, 80, 86, 0.1);
  border-radius: 3px;
  width: 100%;

  &-muted {
    box-shadow: $card-shadow-muted;
  }

  &__body {
    padding: 1.5rem;
  }

  &__header {
    display: flex;
    align-items: center;
    line-height: 1;
    padding: 8px 1.5rem;
    border-bottom: thin solid rgba(61, 80, 86, 0.1);
    font-weight: var(--font-semi-bold);
  }

  &__actions {
    flex: 1;
    display: flex;
    justify-content: flex-end;
  }

  &__title {
    letter-spacing: 0.05rem;
    font-size: 1rem;
    font-weight: var(--font-semi-bold);
    text-transform: capitalize;
  }
  &__subtext {
    font-size: 0.75rem;
    color: var(--txt-normal-color);
    letter-spacing: 1px;
    text-transform: uppercase;
    padding: 0 10px;
  }

  &__text {
    line-height: 1.4;
    font-size: 0.875rem;
    color: var(--txt-normal-color);
    margin-bottom: 0.75rem;
  }

  &__footer {
    position: relative;
    width: 100%;
    // border: 1px dotted lightcoral;
    &::before {
      top: 0;
      left: 0;
      content: "";
      position: absolute;
      background-color: var(--border-color);
      height: 1px;
      width: 100%;
    }
  }
  &__image {
    height: 160px;
    position: relative;
    background-color: var(--bg);
  }
}

.card-login {
  .card__header {
    text-align: center;
    padding-bottom: 1rem;
  }
  .card__body {
    padding-top: 0;
  }
  .card__title {
    font-size: 1.75rem;
  }
  .card_subtext {
    font-size: 1rem;
    color: var(--txt-normal-color);
    font-weight: var(--font-light);
  }
  .card__footer {
    background-color: var(--bg);
    text-align: center;
    * {
      margin-bottom: 0;
    }
  }
}
